@import url('https://fonts.googleapis.com/css2?family=Kameron&family=Libre+Barcode+39+Extended+Text&display=swap');
*{
  padding: 0px;
  margin: 0px;
}
@keyframes perrick {
  0%{
    color: #FF0000;
  }
  20%{
    color: #FF0091;
  }
  40%{
    color: #00FFFF;
  }
  60%{
    color: #15FF00;
  }
  80%{
    color: #F6FF00;
  }
  100%{
    color: white;
  }
}
body{
  background-color: black;
}
.ticker-container{
  position: relative;
  bottom: -10vh;
}
.ticker div{
  color: white;
}
.ticker div:nth-child(even){
  transition: color 0.5s ease-in-out;
  color: red;
}
.phone-wallpaper-copy {
  width: 100%;
  height: 60vh;
  overflow: visible;
  background-image: url(./assets/phone_wallpaper_copy@4x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.links-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: inherit;
  align-items:flex-end;
}
.links-container p{
  padding: 20px;
  text-decoration: underline;
  cursor: pointer;
  font-family: "Kameron", serif;
  font-weight: 400;
  text-decoration: none;
  color: white;
}
.perrick{
  width: auto; /* 121px */
  height: auto; /* 43px */
  overflow: visible;
  white-space: pre;
  -webkit-filter: grayscale(0) hue-rotate(0deg) invert(0) saturate(2) sepia(0) blur(0px);
  filter: grayscale(0) hue-rotate(0deg) invert(0) saturate(2) sepia(0) blur(0px);
  text-shadow: 0px 1px 2px #000000;
  font-weight: 400;
  font-style: normal;
  font-family: "Libre Barcode 39 Extended Text", serif;
  font-size: 36px;
  letter-spacing: 0px;
  line-height: 1.2;
  animation: perrick 5s infinite;
}